      currentFocusTag: "home",
      WinOjbAllGames: [
        {
          gamename: "摇杆测试",
          pic: `./picdir/game1.jpg`, // 这里不能加后缀
          pcpath: "game1",
          Tag: "摇杆",
        },
      ],
    };
  },
  components: {
    AButton: Button,
