  methods: {
    fnoptJump(item: any) {
      // console.log(item);
      // console.log(window.location.href);
      // console.log(window.location.href);
      // console.log(window.location);
      let o = window.location.pathname;
      let foldersAndFile = o.split("/");
      let folders = foldersAndFile.slice(0, foldersAndFile.length - 1);
      let suffix = "/" + item.pcpath + "/index.html";
      let folderpath = folders.join("/");
      // console.log(window.location.origin);
      // console.log(folderpath);
      // window.open(window.location.origin + folderpath, "_blank");
      // window.open(window.location.origin + suffix, "_blank");
      window.open("https://hfbhfb.github.io/" + suffix, "_blank");
    },
    scrollMeTo(refName: any) {
      var element = this.$refs[refName];
      var top = element.offsetTop;

      this.currentFocusTag = refName;

      window.scrollTo(0, top);
    },
    getFocusClass(tag: string) {
      if (tag == this.currentFocusTag) {
        return "focue-menu";
      }
      return "porto-button";
    },
  },
})
export default class HelloWorld extends Vue {
